.logo {
  height: 1.5rem;
  width: 1.5rem;
}
:host {
  display: flex;
  margin-bottom: 5px;
  gap: 5px;
  align-items: center;
}
.icon {
  margin-left: 3px;
  padding: 0;
  margin-top: 0;
  display: flex;
  align-items: center;
}
.label_message {
  margin: 0 3px;
  padding: 0;
}
.label_title {
  min-width: max-content;
}
.label_placeholder {
  color: #cacfe7;
  padding: 0 !important;
  margin: 0 !important;
}
.icon-container {
  margin: 0;
  padding: 0;
}
.country {
  height: 16px;
  width: 23px;
  border-radius: 3px;
}
svg {
  margin: 0;
  padding: 0;
}
